.city__form{
    display: flex;
    justify-content: center;
    margin-top: 40px;
    gap: 20px;
    align-items: center;
}
.city__input{
    padding: 2px;
    outline: 0;
    font-size: 20px;
    border: none;
    border-bottom: 1px solid black;
    font-weight: bold;
    font-size: 25px;
    max-width: 250px;
}
.title{
    margin: 0;
    font-size: 30px;
}
::placeholder {
    color: lightgray;
  }
.sbm__btn{
    border: 1px solid blue;
    border-radius: 10px;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
    background-color: white;
}
@media (max-width: 1000px) {
    .city__form{
        margin-top: 70px;
    }
}
@media (max-width: 800px){
    .city__form{
        flex-direction: column;
        align-items: initial;
        text-align: center;
        gap: 40px;
    }
    .city__input{
        width: 60%;
        max-width:60%;
        margin: 0 auto;
        text-align: center;
    }
    .sbm__btn{
        width: 50%;
        margin: 0 auto;
    }
}