.todayContainer{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 40px;
}
.currentWeather{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid blue;
    border-radius: 10px;
    padding: 15px 40px;
    font-size: 25px;
}
.currentWeather__header{
    display: flex;
    justify-content: space-between;
}
.weatherDate{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
    text-align: right;
    font-size: 30px;
}
.catImg{
    display: flex;
    justify-content: center;
    padding: 10px;
    border: 1px solid orange;
    border-radius: 10px;
}
.catImg img{
    box-sizing: border-box;
    width: 90%;
}
.weatherInfo{
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.weatherInfo__main{
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: space-between;
    font-size: 20px;
    margin-top: 10px;
}
.temp{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 135px;
    font-weight: 400;
}
.weatherImg{
    height: 150px;
}
.mobileDay{
    display: none;
}
@media (max-width: 1200px) {
    .weatherDate{
        font-size: 25px;
        gap: 7px;
    }
    .desktopDay{
        display: none;
    }
    .mobileDay{
        display: block;
    }
    .weatherInfo__main{
        gap: 10px;
        justify-content: center;
    }
    .temp{
        font-size: 110px;
    }
}
@media (max-width: 1000px){
    .todayContainer{
        display: flex;
        flex-direction: column;
        max-width: 500px;
        margin-left: auto;
        margin-right: auto;
        gap: 30px;
    }
    .catImg{
        width: 70%;
        margin: 0 auto;
    }
}
@media (max-width: 530px){
    .temp{
        font-size: 85px;
    }
    .weatherDate{
        font-size: 18px;
        gap: 3px;
    }
    .weatherInfo__main{
        font-size: 15px;
        gap: 5px;
    }
}
@media (max-width: 420px){
    .currentWeather__header{
        flex-direction: column;
    }
    .currentWeather{
        padding: 15px;
        padding-left: 10px;
    }
    .temp{
        display: none;
    }
    .weatherDate{
        text-align: left;
        margin-top: 15px;
    }
    .desktopDay{
        display: initial;
    }
    .mobileDay{
        display: none;
    }
    .weatherInfo__main{
        margin-top: 20px;
    }
    .catImg{
        width: 90%;
    }
}