.iconWrapper{
    width: 25%;
}
.weatherIcon{
    width: 100%;
}
.tempMobile{
    display: none;
}
@media (max-width: 420px) {
    .weatherIcon{
        width: 110px;
    }
    .tempMobile{
        display: block;
        font-size: 75px;
    }
    .iconWrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
}